import React from 'react';
import cx from 'classnames';
import _ from 'lodash';
import pMapSeries from 'p-map-series';
import ShadowDOM from 'react-shadow';
import TimestampIcon from './timestamp.svg';
import CloseIcon from './close.svg';
import ImageIcon from './image.svg';
import DocumentIcon from './document.svg';
import './style.css';
import { xhr, dateTimeDiff } from '../../utils';
import Loader from './loader.js';

class DrawerContainer extends React.Component {
  state = {
    show: false
  };

  handleClose = () => {
    this.setState({
      show: false
    });
  };

  render() {
    return (
      <ShadowDOM
        include={[
          'chrome-extension://hjmnlkneihjloicfbdghgpkppoeiehbf/vandal.css'
        ]}>
        <div>
          {this.state.show && (
            <Drawer
              onClose={this.handleClose}
              getSelectedTS={this.props.getSelectedTS}
            />
          )}
        </div>
      </ShadowDOM>
    );
  }

  componentDidMount() {
    chrome.runtime.onMessage.addListener(request => {
      if (request.message === 'toggleDrawer') {
        this.setState(prevState => {
          return { show: !prevState.show };
        });
      }
    });
  }
}

class Drawer extends React.Component {
  state = {
    showLoader: true,
    scrollOnHighlight: true,
    sources: [],
    timestamps: []
  };

  getIcon(url) {
    if (url.match(/\.(jpeg|jpg|gif|png|svg)$/)) {
      return <ImageIcon className="vandal__drawer__item__image-icon" />;
    } else if (_.endsWith(url, 'js')) {
      return <span className="vandal__drawer__item__resource-icon">JS</span>;
    } else if (_.endsWith(url, 'css')) {
      return <span className="vandal__drawer__item__resource-icon">CSS</span>;
    } else {
      return <DocumentIcon className="vandal__drawer__item__doc-icon" />;
    }
  }

  handleMouseEnter = (source, ts) => e => {
    const { scrollOnHighlight } = this.state;
    chrome.runtime.sendMessage({
      message: 'highlightNode',
      data: { source, ts, scrollOnHighlight }
    });
  };

  handleMouseLeave = () => {
    chrome.runtime.sendMessage({
      message: 'removeHighlight'
    });
  };

  handleScrollOnHighlight = e => {
    this.setState({
      scrollOnHighlight: e.target.checked
    });
  };

  render() {
    const { showLoader, sources, timestamps, scrollOnHighlight } = this.state;
    const { getSelectedTS, onClose } = this.props;
    return (
      <div className="vandal__drawer">
        <div className="vandal__drawer__title-bar">
          <div className="vandal__drawer__title-bar__left">
            <TimestampIcon className="vandal-drawer__timestamp-icon" />
            <div className="vandal__drawer__scroll-container">
              <input
                type="checkbox"
                id="vandal-scroll-highlight"
                checked={scrollOnHighlight}
                className="vandal__drawer__scroll-checkbox"
                onChange={this.handleScrollOnHighlight}
              />
              <label htmlFor="vandal-scroll-highlight">
                Scroll On Highlight
              </label>
            </div>
          </div>
          <CloseIcon className="vandal-drawer__close-icon" onClick={onClose} />
        </div>
        <div className="vandal__drawer__body">
          {showLoader && <div />}
          {!showLoader && !_.isEmpty(sources) && (
            <ul className="vandal__drawer__list">
              {_.map(sources, (source, index) => {
                const dt = dateTimeDiff(timestamps[index], getSelectedTS());
                return (
                  <li
                    key={index}
                    className="vandal__drawer__item"
                    onMouseEnter={this.handleMouseEnter(
                      source,
                      _.get(dt, 'text')
                    )}
                    onMouseLeave={this.handleMouseLeave}>
                    <div className="vandal__drawer__item__left">
                      {timestamps[index] ? this.getIcon(source) : <Loader />}
                      <a
                        href={source}
                        target="_blank"
                        className="vandal__drawer__item__link">
                        {source}
                      </a>
                      <div className="vandal__drawer__item__timestamp">
                        <span
                          className={cx({
                            'vandal__drawer__item__timestamp--plus':
                              _.get(dt, 'delta') >= 0,
                            'vandal__drawer__item__timestamp--minus':
                              _.get(dt, 'delta') < 0
                          })}>
                          {_.get(dt, 'text')}
                        </span>
                        <span>{timestamps[index]}</span>
                      </div>
                    </div>
                  </li>
                );
              })}
            </ul>
          )}
        </div>
      </div>
    );
  }

  fetchSources() {
    chrome.runtime.sendMessage({ message: 'fetchSources' }, resp => {
      console.log(resp);
    });
  }

  async fetchTimestamps(sources) {
    this.timestampController = new AbortController();
    const timestampMapper = async url => {
      const timestamp = await xhr(url, {
        method: 'HEAD',
        fetchResHeader: 'Memento-Datetime'
      });

      this.setState({
        timestamps: [...this.state.timestamps, timestamp]
      });
    };

    await pMapSeries(sources, timestampMapper);
  }

  async componentDidMount() {
    this.fetchSources();
    chrome.runtime.onMessage.addListener(request => {
      if (request.message === 'URLLoading') {
        this.setState({ sources: [], showLoader: true });
      } else if (request.message === 'URLLoaded') {
        this.fetchSources();
      } else if (request.message === 'frameSource') {
        this.setState({
          sources: request.data,
          timestamps: [],
          showLoader: false
        });
        this.fetchTimestamps(request.data);
      }
    });
  }
}

export default DrawerContainer;
