import _ from 'lodash';

export const api = async (
  endpoint,
  { controller, noCacheReq, noCacheRes, fetchResHeaders, method = 'GET' } = {}
) => {
  let request;
  if (controller) {
    const signal = controller.signal;
    request = new Request(endpoint, { signal });
  } else {
    request = new Request(endpoint);
  }

  if (!noCacheReq) {
    const resFromCache = await caches.match(request);
    if (_.get(resFromCache, 'status') === 200) {
      return getResponse(resFromCache);
    }
  }

  try {
    const resFromFetch = await fetch(request.clone(), { method });
    if (!noCacheRes && _.get(resFromFetch, 'status') === 200) {
      const cache = await caches.open('__VANDAL__');
      cache.put(request, resFromFetch.clone());
    }

    return _.get(resFromFetch, 'status') === 200
      ? await getResponse(resFromFetch, fetchResHeaders)
      : Promise.reject();
  } catch (err) {
    console.log('error: ', err);
  }
};

const getResponse = async (res, fetchResHeaders) => {
  if (fetchResHeaders) {
    return _.get(res, 'headers');
  } else if (_.endsWith(_.get(res, 'url'), '.png')) {
    const responseBlob = await res.blob();
    return URL.createObjectURL(responseBlob);
  } else {
    return await res.json();
  }
};

export const xhr = (url, { method = 'GET', fetchResHeader }) => {
  return new Promise(function(resolve, reject) {
    let xhrRequest = new XMLHttpRequest();
    xhrRequest.open(method, url);
    xhrRequest.onload = function() {
      if (this.status >= 200 && this.status < 300) {
        if (fetchResHeader) {
          return resolve(xhrRequest.getResponseHeader(fetchResHeader));
        }
        resolve(xhrRequest.response);
      } else {
        reject({
          status: this.status,
          statusText: xhrRequest.statusText
        });
      }
    };
    xhrRequest.onerror = function() {
      reject({
        status: this.status,
        statusText: xhrRequest.statusText
      });
    };
    xhrRequest.send();
  });
};
