import React from 'react';
import ReactTooltip from 'react-tooltip';
import _ from 'lodash';
import Transition from 'react-transition-group/Transition';
import pMapSeries from 'p-map-series';
import pMap from 'p-map';
import ImageLoader from './ImageLoader';
import { VerticalMenu } from '../Common';
import { api, toTwelveHourTime, getDateTsFromUrl } from '../../utils';

const monthNames = [
  'January',
  'February',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December'
];

const duration = 300;
const height = 210;

const options = [
  {
    value: 'openURL',
    text: 'Open URL'
  },
  {
    value: 'retry',
    text: 'Retry'
  }
];

const transitionStates = {
  entering: {
    transform: `translateY(${height}px)`
  },
  entered: {
    transform: 'translateY(0)'
  },
  exiting: {
    transform: 'translateY(0)'
  },
  exited: {
    transform: `translateY(${height}px)`
  }
};

const transitionStyle = {
  transition: `all ${duration}ms ease-in-out`,
  transitionProperty: 'transform',
  willChange: 'transform',
  height: `${height}px`,
  transform: 'translateY(0)'
};

export default class MonthView extends React.Component {
  state = {
    snapshots: [],
    snapshotUrls: [],
    isLoading: false
  };

  handleExit = () => {
    this.snapshotController && this.snapshotController.abort();
    this.archiveController && this.archiveController.abort();
    this.setState({ snapshots: [], snapshotUrls: [], isLoading: false });
    this.props.onExited();
  };

  onEntered = _.debounce(this.props.onEntered, 250);
  onDebouncedExited = _.debounce(this.handleExit, 250);

  handleMouseWheel = e => {
    e.stopPropagation();
    var max = this.scrollWidth - this.offsetWidth; // this might change if you have dynamic content, perhaps some mutation observer will be useful here
    if (this.scrollLeft + e.deltaX < 0 || this.scrollLeft + e.deltaX > max) {
      e.preventDefault();
      this.scrollLeft = Math.max(0, Math.min(max, this.scrollLeft + e.deltaX));
    }
    return false;
  };

  handleOptions = index => async option => {
    if (option === 'retry') {
      const { snapshots } = this.state;
      snapshots[index] = null;
      this.setState({
        snapshots: [...snapshots]
      });

      const snapshotPath = await api(
        `https://zzgi3cdqi7.execute-api.us-east-2.amazonaws.com/dev/screenshot?url=${
          this.state.snapshotUrls[index]
        }`,
        {
          noCacheReq: true
        }
      );
      const snapshot = `https://d1smdru0lrhqpr.cloudfront.net/${encodeURIComponent(
        snapshotPath
      )}`;

      snapshots[index] = snapshot;
      this.setState({
        snapshots: [...snapshots]
      });
    }
  };

  async componentDidMount() {
    if (!this.monthView) return;
    this.monthView.addEventListener('mousewheel', this.handleMouseWheel, {
      passive: false,
      capture: true
    });
    this.scrollWidth = this.monthView.scrollWidth;
    this.offsetWidth = this.monthView.offsetWidth;
  }

  componentWillReceiveProps(nextProps) {
    if (
      nextProps.year &&
      nextProps.show &&
      (nextProps.year !== this.props.year ||
        (!this.state.isLoading && _.size(this.state.snapshots) < 12))
    ) {
      this.setState({ snapshots: [], isLoading: true });
      this.getSnapShots(nextProps);
    }
  }

  async getSnapShots(props) {
    const { year, url } = props;
    const currentYear = new Date().getFullYear();
    const currentMonth = new Date().getMonth();
    // Map serially, as archive times out if done concurrently
    const timestampUrls = _.map(monthNames, (__, index) => {
      return `https://archive.org/wayback/available?url=${url}&timestamp=${year}${_.padStart(
        _.toString(index + 1),
        2,
        0
      )}`;
    });

    this.archiveController = new AbortController();
    const monthMapper = async (url, index) => {
      return await api(url, {
        controller: this.archiveController,
        noCacheReq: currentYear === year && index >= currentMonth,
        noCacheRes: currentYear === year && index >= currentMonth
      });
    };
    const timestampResult = await pMapSeries(timestampUrls, monthMapper);

    const snapshotUrls = _.map(timestampResult, url =>
      _.replace(
        _.replace(_.get(url, 'archived_snapshots.closest.url'), /\d+/, '$&im_'),
        /https?/,
        'https'
      )
    );
    console.log('snapshotUrls: ', snapshotUrls);

    if (_.size(_.compact(snapshotUrls)) < 12) {
      return;
    }

    this.snapshotController = new AbortController();
    const snapshotMapper = async (url, index) => {
      const snapshotPath = await api(
        `https://zzgi3cdqi7.execute-api.us-east-2.amazonaws.com/dev/screenshot?url=${url}`,
        {
          controller: this.snapshotController,
          noCacheReq: currentYear === year && index >= currentMonth,
          noCacheRes: currentYear === year && index >= currentMonth
        }
      );
      const snapshot = `https://d1smdru0lrhqpr.cloudfront.net/${encodeURIComponent(
        snapshotPath
      )}`;
      this.setState({
        snapshotUrls,
        snapshots: [...this.state.snapshots, snapshot]
      });
      return snapshot;
    };

    await pMap(snapshotUrls, snapshotMapper);

    this.setState({ isLoading: false });
  }

  getSnapShotUrl(index) {
    return this.state.snapshots[index] || null;
  }

  render() {
    const { snapshotUrls } = this.state;
    return (
      <Transition
        enter
        appear
        in={this.props.show}
        timeout={{ enter: duration, exit: 0 }}
        onEntered={this.onEntered}
        onExited={this.onDebouncedExited}>
        {state => (
          <div
            className="vandal-historical-month-view"
            ref={_ref => (this.monthView = _ref)}
            style={{
              ...transitionStyle,
              ...transitionStates[state]
            }}>
            <div
              className="vandal-historical-month-close-container"
              onClick={this.props.onClose}>
              <div className="vandal-arrow-down" />
            </div>
            <div className="vandal-historical-month-scroll-view">
              {_.map(monthNames, (month, index) => {
                const dateObj = getDateTsFromUrl(snapshotUrls[index]);
                return (
                  <div
                    className="vandal-historical-month"
                    key={`${this.props.year}-${month}`}>
                    <div
                      className="vandal-historical-month__body"
                      onClick={this.props.openMonth(
                        index,
                        this.state.snapshots
                      )}>
                      {this.getSnapShotUrl(index) ? (
                        <React.Fragment>
                          <img
                            src={this.getSnapShotUrl(index)}
                            className="vandal-historical-month__snapshot"
                          />
                          <div
                            className="vandal-historical-month__info"
                            data-for={`vandal-historical-month--info-${month}`}
                            data-tip={`Captured on ${
                              dateObj.date
                            }, ${toTwelveHourTime(dateObj.time)}`}>
                            i
                          </div>
                          <ReactTooltip
                            className="vandal-historical-month--info-tooltip"
                            id={`vandal-historical-month--info-${month}`}
                            effect="solid"
                            place="right"
                            outsidePlace="left"
                            scrollHide
                            insecure={false}
                          />
                        </React.Fragment>
                      ) : (
                        <ImageLoader />
                      )}
                    </div>
                    <div className="vandal-historical-month__footer">
                      <div className="vandal-historical-month__value">
                        {month}
                      </div>
                      <VerticalMenu
                        options={options}
                        onSelect={this.handleOptions(index)}
                      />
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
        )}
      </Transition>
    );
  }
}
