import React from 'react';
import ReactTooltip from 'react-tooltip';
import _ from 'lodash';
import cx from 'classnames';
import pMapSeries from 'p-map-series';
import pMap from 'p-map';
import MonthView from './MonthView';
import { api, toTwelveHourTime, getDateTsFromUrl } from '../../utils';
import { VerticalMenu } from '../Common';
import ImageLoader from './ImageLoader';
import CarouselView from './CarouselView';
import './style.css';

const monthNames = [
  'January',
  'February',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December'
];

const options = [
  {
    value: 'showMonths',
    text: 'Toggle Month View'
  },
  {
    value: 'goToURL',
    text: 'Go to URL'
  },
  {
    value: 'retry',
    text: 'Retry'
  }
];

export default class HistoricalView extends React.Component {
  state = {
    selectedYear: null,
    showCarousel: null,
    showMonthView: false,
    resizeYearContainer: false,
    selectedIndex: 0,
    snapshots: [],
    snapshotUrls: [],
    years: _.keys(this.props.sparkline)
  };

  handleOptions = (index, year) => async option => {
    if (option === 'retry') {
      const { snapshots, snapshotUrls } = this.state;
      const { url } = this.props;
      snapshots[index] = null;
      this.setState({
        snapshots: [...snapshots]
      });

      let snapshotUrl = snapshotUrls[index];
      if (!snapshotUrl) {
        snapshotUrl = await api(
          `https://archive.org/wayback/available?url=${url}&timestamp=${year}12`,
          {
            controller: this.retryController,
            noCacheReq: true
          }
        );
        snapshotUrl = _.replace(
          _.replace(
            _.get(snapshotUrl, 'archived_snapshots.closest.url'),
            /\d+/,
            '$&im_'
          ),
          /https?/,
          'https'
        );
      }

      const snapshotPath = await api(
        `https://zzgi3cdqi7.execute-api.us-east-2.amazonaws.com/dev/screenshot?url=${snapshotUrl}`,
        {
          noCacheReq: true
        }
      );
      const snapshot = `https://d1smdru0lrhqpr.cloudfront.net/${encodeURIComponent(
        snapshotPath
      )}`;

      snapshots[index] = snapshot;
      snapshotUrls[index] = snapshotUrl;
      this.setState({
        snapshots: [...snapshots],
        snapshotUrls: [...snapshotUrls]
      });
    } else if (option === 'showMonths') {
      if (
        this.state.showMonthView &&
        this.state.selectedYear === this.state.years[index]
      ) {
        this.setState({ selectedYear: null, showMonthView: false });
      } else {
        this.setState({
          selectedYear: this.state.years[index],
          showMonthView: true
        });
      }
    }
  };

  handleEntered = () => {
    this.setState({ resizeYearContainer: true });
  };

  handleExited = () => {
    this.setState({ resizeYearContainer: false });
  };

  handleClose = () => {
    this.setState({ showMonthView: false, selectedYear: null });
  };

  getSnapShotUrl = index => {
    return this.state.snapshots[index] || null;
  };

  openYear = selectedIndex => () => {
    this.setState({
      showCarousel: 'year',
      selectedIndex,
      images: _.map(this.state.years, (__, index) => {
        return this.getSnapShotUrl(index);
      })
    });
  };

  openMonth = (selectedIndex, images) => () => {
    this.setState({
      images,
      selectedIndex,
      showCarousel: 'month'
    });
  };

  handleCarouselClose = () => {
    this.setState({
      showCarousel: null
    });
    if (this.historicalContainerRef) {
      this.historicalContainerRef.focus();
    }
  };

  getCaption = index => {
    if (this.state.showCarousel === 'month') {
      return { title: this.state.selectedYear, date: monthNames[index] };
    }
    return { title: 'YEAR', date: this.state.years[index] };
  };

  handleHistoricalClose = () => {
    this.props.onClose();
  };

  abort = () => {
    this.snapshotController && this.snapshotController.abort();
    this.archiveController && this.archiveController.abort();
    this.retryController && this.retryController.abort();
  };

  handleKeydown = e => {
    if (e.keyCode === 27) {
      this.handleHistoricalClose();
    }
  };

  render() {
    const {
      selectedYear,
      showMonthView,
      resizeYearContainer,
      showCarousel,
      images,
      selectedIndex,
      snapshotUrls,
      years
    } = this.state;

    return (
      <div
        className="vandal-historical-view"
        onKeyDown={this.handleKeydown}
        ref={_ref => (this.historicalContainerRef = _ref)}
        tabIndex="0">
        <div
          className={cx({
            'vandal-historical-year-container': true,
            'vandal-historical-year-container--month-view': resizeYearContainer
          })}>
          {_.map(years, (year, index) => {
            const dateObj = getDateTsFromUrl(snapshotUrls[index]);
            return (
              <div
                className={cx({
                  'vandal-historical-year': true,
                  'vandal-historical-year--selected': selectedYear === year
                })}
                key={year}>
                <div
                  className="vandal-historical-year__body"
                  onClick={this.openYear(index)}>
                  {this.getSnapShotUrl(index) ? (
                    <React.Fragment>
                      <img
                        className="vandal-historical-year__snapshot"
                        src={this.getSnapShotUrl(index)}
                      />
                      <div className="vandal-historical-year__body__highlight" />
                      <div
                        className="vandal-historical-year__info"
                        data-for={`vandal-historical-year--info-${year}`}
                        data-tip={`Captured on ${
                          dateObj.date
                        }, ${toTwelveHourTime(dateObj.time)}`}>
                        i
                      </div>
                      <ReactTooltip
                        className="vandal-historical-year--info-tooltip"
                        id={`vandal-historical-year--info-${year}`}
                        effect="solid"
                        place="right"
                        insecure={false}
                      />
                    </React.Fragment>
                  ) : (
                    <ImageLoader />
                  )}
                </div>
                <div className="vandal-historical-year__footer">
                  <div className="vandal-historical-year__value">{year}</div>
                  <VerticalMenu
                    options={options}
                    onSelect={this.handleOptions(index, year)}
                  />
                </div>
              </div>
            );
          })}
        </div>
        <MonthView
          url={this.props.url}
          year={selectedYear}
          show={showMonthView}
          onEntered={this.handleEntered}
          onExited={this.handleExited}
          onClose={this.handleClose}
          openMonth={this.openMonth}
        />
        {showCarousel && (
          <CarouselView
            images={images}
            selectedIndex={selectedIndex}
            getCaption={this.getCaption}
            onClose={this.handleCarouselClose}
          />
        )}
      </div>
    );
  }

  componentDidMount() {
    document.body.style.overflow = 'hidden';
    if (this.historicalContainerRef) {
      this.historicalContainerRef.focus();
    }
  }

  componentWillUnmount() {
    this.abort();
    document.body.style.overflow = '';
  }

  async componentWillMount() {
    await this.getSnapshots(this.props);
  }

  async componentWillReceiveProps(nextProps) {
    await this.getSnapshots(nextProps);
  }

  async getSnapshots(props) {
    const years = _.keys(props.sparkline);
    if (_.isEmpty(years)) return;

    const { url } = this.props;

    // Map serially, as archive times out if done concurrently
    const timestampUrls = _.map(years, y => {
      return {
        url: `https://archive.org/wayback/available?url=${url}&timestamp=${y}12`,
        y
      };
    });

    this.archiveController = new AbortController();
    const yearMapper = async (item, index) => {
      return await api(item.url, {
        controller: this.archiveController,
        noCacheReq: _.size(timestampUrls) - 1 === index,
        noCacheRes: _.size(timestampUrls) - 1 === index
      });
    };
    const timestampResult = await pMapSeries(timestampUrls, yearMapper);

    const snapshotUrls = _.map(timestampResult, url =>
      _.replace(
        _.replace(_.get(url, 'archived_snapshots.closest.url'), /\d+/, '$&im_'),
        /https?/,
        'https'
      )
    );
    console.log('snapshotUrls: ', snapshotUrls);

    this.snapshotController = new AbortController();
    const snapshotMapper = async (url, index) => {
      let snapshot;
      if (url) {
        const isLast = _.size(snapshotUrls) - 1 === index;
        const snapshotPath = await api(
          `https://zzgi3cdqi7.execute-api.us-east-2.amazonaws.com/dev/screenshot?url=${url}`,
          {
            controller: this.snapshotController,
            noCacheReq: isLast,
            noCacheRes: isLast
          }
        );
        const encodedSnapShot = encodeURIComponent(snapshotPath);
        snapshot = await api(
          `https://d1smdru0lrhqpr.cloudfront.net/${encodedSnapShot}`,
          {
            noCacheReq: isLast,
            noCacheRes: isLast
          }
        );
      }

      this.setState({
        snapshotUrls,
        snapshots: [...this.state.snapshots, snapshot]
      });

      return snapshot;
    };

    await pMapSeries(snapshotUrls, snapshotMapper);
  }
}
