import React from "react";
import ReactTooltip from "react-tooltip";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import HistoricalView from "../HistoricalView";
import DiffView from "../DiffView";
import CloseIcon from "./close.svg";
import "./style.css";

export default class Mode extends React.Component {
  state = {
    selectedTabIndex: 0
  };

  handleTabChange = selectedTabIndex => {
    this.setState({ selectedTabIndex });
    if (this.historicalViewRef) {
      this.historicalViewRef.abort();
    }
  };

  handleKeydown = e => {
    if (e.keyCode === 27) {
      this.props.onClose();
    }
  };

  componentDidMount() {
    document.body.style.overflow = "hidden";
    if (this.modeRef) {
      this.modeRef.focus();
    }
  }

  render() {
    const { sparkline, url, mode } = this.props;

    return (
      <div
        className="vandal-mode-view"
        onKeyDown={this.handleKeydown}
        ref={_ref => (this.modeRef = _ref)}
        tabIndex="0"
      >
        <Tabs
          className="vandal-mode__tabs"
          selectedTabClassName="vandal-mode__tab--active"
          onSelect={this.handleTabChange}
          defaultIndex={mode === "histView" ? 0 : 1}
        >
          <TabList className="vandal-mode__tab-list">
            <Tab className="vandal-mode__tab vandal-mode__tab-historical">
              Historical
            </Tab>
            <Tab className="vandal-mode__tab vandal-mode__tab-diff">Diff</Tab>
          </TabList>

          <TabPanel className="vandal-mode__tab-panel">
            <HistoricalView
              ref={_ref => (this.historicalViewRef = _ref)}
              sparkline={sparkline}
              url={url}
              onClose={this.props.onClose}
            />
          </TabPanel>
          <TabPanel className="vandal-mode__tab-panel">
            <DiffView
              ref={_ref => (this.diffViewRef = _ref)}
              sparkline={sparkline}
              url={url}
              onClose={this.props.onClose}
            />
          </TabPanel>
        </Tabs>
        <CloseIcon
          data-for="vandal-mode--close"
          data-tip="Close [Esc]"
          className="vandal-mode__close"
          onClick={this.props.onClose}
        />
        <ReactTooltip
          id="vandal-mode--close"
          effect="solid"
          place="bottom"
          insecure={false}
        />
      </div>
    );
  }
}
