import React from 'react';
import { findDOMNode } from 'react-dom';
import _ from 'lodash';
import cx from 'classnames';
import VerticalMenuIcon from './vertical-menu.svg';
import './style.css';

export default class VerticalMenu extends React.PureComponent {
  static defaultProps = {
    onSelect: () => {},
    show: null
  };

  state = {
    visible: false
  };

  handleOption = value => event => {
    event.preventDefault();
    event.stopPropagation();
    this.props.onSelect(value);
    this.setState({ visible: false });
  };

  toggleMenu = e => {
    e.preventDefault();
    e.stopPropagation();
    this.setState(prevState => ({ visible: !prevState.visible }));
  };

  hideMenu() {
    if (this.state.visible) this.setState({ visible: false });
  }

  handleClickOutside = event => {
    if (
      this.state.visible &&
      this.wrapperRef &&
      _.indexOf(event.composedPath(), findDOMNode(this.wrapperRef)) === -1
    ) {
      console.log(event.target, event.composedPath());
      this.setState({ visible: false });
    }
  };

  componentDidMount() {
    document.addEventListener('mousedown', this.handleClickOutside);
  }

  componentWillUnmount() {
    document.removeEventListener('mousedown', this.handleClickOutside);
  }

  render() {
    const { options } = this.props;
    const { visible } = this.state;

    return (
      <div
        className="vandal-vertical-menu"
        ref={_ref => (this.wrapperRef = _ref)}>
        <div
          className={cx({
            'vandal-vertical-menu-icon-container': true,
            'vandal-vertical-menu-icon-container--selected': visible
          })}
          onClick={this.toggleMenu}>
          <VerticalMenuIcon className="vandal-vertical-menu-icon" />
        </div>
        {visible && (
          <ul className="vandal-vertical-menu__list">
            {_.map(options, (option, index) => {
              return option.title ? (
                <li className="vandal-vertical-menu__list__item" key={index}>
                  {option.text}
                </li>
              ) : (
                <li
                  className="vandal-vertical-menu__list__item"
                  key={index}
                  onMouseDown={this.handleOption(option.value)}>
                  {option.text}
                </li>
              );
            })}
          </ul>
        )}
      </div>
    );
  }
}
