import React from 'react';
import cx from 'classnames';
import './style.css';
import HistoryIcon from './history.svg';
import InfoIcon from './info.svg';
import GlobeIcon from './globe.svg';
import ArchiveIcon from './archive.svg';
import { ArchiveLoader, URLLoader } from '../Common';
import { getDateTimeFromTS, toTwelveHourTime } from '../../utils';

export default class URLBox extends React.Component {
  static defaultProps = {
    toggleTimeTravel: () => {},
    url: ''
  };

  state = {
    showInfo: false
  };

  getRef() {
    return this.urlFilterRef;
  }

  render() {
    const {
      toggleTimeTravel,
      toggleURLInfo,
      showTimeTravel,
      showURLInfo,
      selectedTS,
      redirectedTS,
      showFrameLoader,
      showURLLoader,
      url
    } = this.props;
    const ts = redirectedTS || selectedTS;
    const dateObj = ts ? getDateTimeFromTS(ts) : {};
    return (
      <div
        className={cx({
          'vandal-url-container': true,
          'vandal-url-container--timetravel': showTimeTravel
        })}>
        <div className="vandal-url__mode">
          {showURLLoader && <URLLoader />}
          {!ts && !showURLLoader && <GlobeIcon className="vandal-url__icon" />}
          {!!ts && !showURLLoader && (
            <ArchiveIcon className="vandal-archive__icon" />
          )}
        </div>
        <input type="text" className="vandal-url__input" value={url} readOnly />
        {!!ts && (
          <div
            className={cx({
              'vandal-url__date': true,
              'vandal-url__date-loader': showFrameLoader,
              'vandal-url__date-info': showURLInfo
            })}
            onClick={toggleURLInfo}>
            {showFrameLoader && <ArchiveLoader title="Loading..." />}
            {!showFrameLoader && (
              <InfoIcon width={22} className="vandal-url__date-info__icon" />
            )}
            <div className="vandal-url__date__value">
              <span style={{ marginRight: '3px' }}>
                {dateObj.humanizedDate}
              </span>{' '}
              <span>{toTwelveHourTime(dateObj.ts)}</span>
            </div>
          </div>
        )}
        <div
          className="vandal-url__filter"
          ref={_ref => (this.urlFilterRef = _ref)}>
          <div
            className={cx({
              'vandal-url__timetravel': true,
              'vandal-url__timetravel--selected': showTimeTravel
            })}
            onClick={toggleTimeTravel}>
            <HistoryIcon
              width={22}
              fill={'#777'}
              className="vandal-url__timetravel__icon"
            />
          </div>
        </div>
      </div>
    );
  }
}
